@use "../abstracts/mixins" as *;

.container {
    --column-count: 1;

    display: grid;
    grid-template-columns: repeat(var(--column-count), 1fr);
    place-items: center;
    row-gap: 20px;

    max-width: 768px;
    padding: 20px 0 40px;
    margin-inline: auto;

    @include mq(mobile, and, tablet) {
        padding: 20px 15px 40px;
    }
    @include mq(tablet, and, desktop) {
        --column-count: 2;
        gap: 32px;
    }

    @include mq(tablet) {
        padding: 60px 32px;
    }
    @include mq(desktop) {
        --column-count: 3;
        max-width: 1280px;
        gap: 32px 16px;
    }
}
.is-hidden {
    visibility: hidden;
    pointer-events: none;
}
.link {
    text-decoration: none;
}
.uppercase {
    text-transform: uppercase;
}
[aria-current="page"] {
    border-bottom: 3px solid red;
}
